.button-color {
  /* border: 2px solid #5e5e5e; */
  border: none;
  border-radius: 5px;
  margin: 5px;
  cursor: pointer;
  width: 300px;
  height: 300px;
  opacity: 0.4;
  transition: 0.1s;
}

.button-color:active {
  opacity: 1;
}

.red {
  border-top-left-radius: 100%;
  background-color: #FF0060;
}

