.center {
  width: 200px;
  height: 200px;
  /* border: 2px solid #5e5e5e; */
  border-radius: 50%;
  background-color: #333333;
  position: absolute;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;

}

.center-button {
  width: 90%;
  height: 90%;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #FFFFFF;
  background-color: #555555;
}

.center-button p {
  font-size: 1.2rem;
  font-family: 'Pixelify Sans', sans-serif;
}