@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@100;200;300;400;500;600;700&family=Pixelify+Sans:wght@400;600&display=swap');

@keyframes pulseStartButton {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}

.background-game {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: #333333;
}

.color-buttons {
  width: auto;
  height: auto;
}

.color-buttons-area {
  display: flex;
  flex-direction: row;
  width: auto;
  height: auto;
  justify-content: space-between;
}

.action-button-area {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
}

.action-button {
  width: auto;
  height: auto;
  padding: 10px;
  color: #ffffff;
  font-size: medium;
  font-weight: bold;
  background-color: #99bc85;
  border: none;
  border-radius: 30px;
  transition: ease-in 3s;
}


.start-button {
  width: auto;
  height: auto;
  border-radius: 20px;
  background-color: #FD8D14;
  color: #FFFFFF;
  /* position: absolute; */
  font-family: 'Pixelify Sans', sans-serif;
  font-weight: medium;
  font-size: 4rem;
  animation: pulseStartButton .8s infinite alternate;
}


.status-text {
  /* font-family: 'Barlow', sans-serif; */
  font-family: 'Pixelify Sans', sans-serif;
}

.board-buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.comments-area {
  width: 100vw;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: red; */
}

.comments-text {
  font-size: 2.5rem;
  color: #FFFFFF;
  font-weight: normal;
  font-family: 'Pixelify Sans', sans-serif;
}